
body {
  font-size: 16px;
}
*,
*:before,
*:after {
  box-sizing: border-box;
}
#app {
  font-family: 'Helvetica Neue', Arial, 'Open Sans', 'Hiragino Sans GB', -apple-system, 'Lantinghei SC', 'Microsoft Yahei', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}


